import React from "react";
import { connect } from "react-redux";
import { Helmet } from 'react-helmet';
import { Card, CardBody, CardHeader, CardTitle, Row, Col } from "reactstrap"
import Breadcrumbs from "../../../../components/@vuexy/breadCrumbs/BreadCrumb"
import AdministrativeTypesTable from './Tables/AdministrativeTypesTable';
import AdministrativeTypesModal from './Modals/AdministrativeTypesModal';
import SystemStatusTypeSearch from '../../SearchComponents/SystemStatusTypeSearch/SystemStatusTypeSearch';
import * as API from "../../../../utility/crud/app.crud";
import * as Date from "../../../../utility/dateAndTime";
import { toastr } from 'react-redux-toastr'

class AdministrativeTypes extends React.Component {

    constructor(props, context) {
        super(props, context);

        this.state = {
            validated: false,
            errors: {},
            columns: [],
            data: [],
            filteredData: [],
            value: '',
            show: false,
            showStatus: true,
            showdelete: false,
            Dropdownvalidation: 0,
            ADMIN_TYPE_ID: 0,
            ADMIN_DESCRIPTION: '',
            SYS_STATUS_ID: 0,
            CREATEDBY: '',
            MODIFIEDBY: '',
            CREATED_DATE: Date.ShowDate(),
            MODIFIED_DATE: Date.ShowDate(),
            InsertUpdate: '',

            opensearch: false,
            SYSTEMSTATUSTYPEDESCRIPTION: '',
            searchcolumns: [],
            searchdata: [],
            searchFilteredData: [],
            searchvalue: '',
        }

        this.handleShow = () => {
            this.setState({
                show: true,
                InsertUpdate: 'Insert'
            });
            this.clearData();
        }

        this.handleHide = () => {
            this.setState({
                show: false
            });
        }

        this.handleHideDelete = () => {
            this.setState({
                showdelete: false
            });
        }
        this.handleShowSearch = () => {
            this.setState({ opensearch: true });
        };

        this.handleHideSearch = () => {
            this.setState({ opensearch: false });
        };

        this.populateData();
        this.populateStatus();
    }
    handleValidationDropDown(value) {
        let errors = {};
        let formIsValid = true;
        if (value === "Status") {
            errors["Status"] = "";
        }

        this.setState({ errors: errors });
        return formIsValid;
    }
    handleValidation() {
        debugger;
        let errors = {};
        let formIsValid = true;
        if (!this.state.Description) {
            formIsValid = false;
        }

        if (!this.state.SYSTEMSTATUSTYPEDESCRIPTION) {
            formIsValid = false;
            errors["Status"] = "Please Select Status";
        }


        this.setState({ errors: errors });
        return formIsValid;
    }
    populateData() {
        const getdata = `
            query{
                AdministrativeTypes{
                    ADMIN_TYPE_ID
                    ADMIN_DESCRIPTION
                    SYS_STATUS_ID
                    SYSTEMSTATUS{
                        SYS_STATUS_ID
                        SYS_STATUS_NAME
                    }
                    CREATEDBY
                    MODIFIEDBY
                    CREATED_DATE
                    MODIFIED_DATE
                }
            }
        `;

        API.getData(getdata)
            .then(({ data }) => this.setState({
                data: data.data.AdministrativeTypes
            })).catch(error => {
                if (error.response || error.request) {
                    toastr.error('Error Loading Data', 'Failed to Load Data');
                }
            })

    }

    populateStatus() {
        const getdata = `
            query {
                SystemStatusFilter (
                    CATEGORY:"SET"
                    ){
                        SYS_STATUS_ID,
                        SYS_STATUS_NAME
                }
            }
        `;

        API.getData(getdata)
            .then(({ data }) => this.setState({
                searchdata: data.data.SystemStatusFilter
            })).catch(error => {
                if (error.response || error.request) {
                    toastr.error('Error Loading Data', 'Failed to System Status Type Load Data');
                }
            })
    }

    handleSubmit = (event) => {
        event.preventDefault();
        const formadmin = event.currentTarget;
        //&& this.handleValidation() === false
        if (formadmin.checkValidity() === false) {
            event.preventDefault();
            event.stopPropagation();
        }
        else {
            const form = `
        mutation{
            addAdministrativeType(
                ADMIN_DESCRIPTION:"${this.state.ADMIN_DESCRIPTION}",
                SYS_STATUS_ID:${this.state.SYS_STATUS_ID},
                CREATEDBY:"${this.state.CREATEDBY}",
                MODIFIEDBY:"${this.state.MODIFIEDBY}",
                CREATED_DATE:"${this.state.CREATED_DATE}",
                MODIFIED_DATE:"${this.state.MODIFIED_DATE}"
                ){
                    ADMIN_TYPE_ID
            }
        }
        `;

            const formEdit = `
        mutation{
            updateAdministrativeType(
                ADMIN_TYPE_ID:${this.state.ADMIN_TYPE_ID},
                ADMIN_DESCRIPTION:"${this.state.ADMIN_DESCRIPTION}",
                SYS_STATUS_ID:${this.state.SYS_STATUS_ID},
                CREATEDBY:"${this.state.CREATEDBY}",
                MODIFIEDBY:"${this.state.MODIFIEDBY}",
                CREATED_DATE:"${this.state.CREATED_DATE}",
                MODIFIED_DATE:"${this.state.MODIFIED_DATE}"
                ){
                    ADMIN_TYPE_ID
            }
        }
        `;

            if (this.state.InsertUpdate === 'Insert') {
                API.create(form)
                    .then(() => {
                        this.clearData();
                        this.handleHide();
                        this.populateData();
                        toastr.success('Create Record', 'Record Created Successfully');
                    }).catch(error => {
                        if (error.response || error.request) {
                            toastr.error('Error Loading Data', 'Failed to Load Data');
                        }
                    })
            } else {
                API.update(formEdit)
                    .then(() => {
                        this.clearData();
                        this.handleHide();
                        this.populateData();
                        toastr.info('Update Record', 'Record Updated Successfully');
                    }).catch(error => {
                        if (error.response || error.request) {
                            toastr.error('Error Loading Data', 'Failed to Load Data');
                        }
                    })
            }
        }
        this.setState({
            validated: true
        })
    }

    handleDelete = (event) => {
        event.preventDefault();

        const formDelete = `
        mutation{
            deleteAdministrativeType(
                ADMIN_TYPE_ID:${this.state.ADMIN_TYPE_ID}
                ){
                    ADMIN_TYPE_ID
            }
        }
        `;

        API.remove(formDelete)
            .then(() => {
                this.handleHideDelete();
                this.populateData();
                toastr.warning('Delete Record', 'Record Deleted Successfully');
            }).catch(error => {
                if (error.response || error.request) {
                    toastr.error('Error Loading Data', 'Failed to Load Data');
                }
            })

    }

    handleCloseSearch = (e) => {

        this.setState({
            SYS_STATUS_ID: e.SYS_STATUS_ID,
            SYSTEMSTATUSTYPEDESCRIPTION: e.SYS_STATUS_NAME,
            opensearch: false,

        });
        //  this.handleValidationDropDown("Status");

    };

    handleEditData = (e) => {
        this.setState({
            ADMIN_TYPE_ID: e.ADMIN_TYPE_ID,
            ADMIN_DESCRIPTION: e.ADMIN_DESCRIPTION,
            SYS_STATUS_ID: e.SYS_STATUS_ID,
            SYSTEMSTATUSTYPEDESCRIPTION: e.SYSTEMSTATUS.SYS_STATUS_NAME,
            show: true,
            InsertUpdate: 'Update'
        })
    }

    handleDeleteData = (e) => {
        this.setState({
            ADMIN_TYPE_ID: e.ADMIN_TYPE_ID,
            ADMIN_DESCRIPTION: e.ADMIN_DESCRIPTION,
            showdelete: true,
            InsertUpdate: 'Delete'
        })
    }

    clearData() {
        this.setState({
            ADMIN_DESCRIPTION: '',
            SYS_STATUS_ID: '',
            SYSTEMSTATUSTYPEDESCRIPTION: '',
            validated: false
        })
    }

    handleFilter = (e) => {
        let value = e.target.value
        let data = this.state.data
        let filteredData = this.state.filteredData
        this.setState({ value });

        if (value.length) {
            filteredData = data.filter(item => {
                let startsWithCondition =
                    item.SYSTEMSTATUS.SYS_STATUS_NAME.toString().toLowerCase().startsWith(value.toLowerCase()) ||
                    item.ADMIN_DESCRIPTION.toString().toLowerCase().startsWith(value.toLowerCase())

                let includesCondition =
                    item.SYSTEMSTATUS.SYS_STATUS_NAME.toString().toLowerCase().includes(value.toLowerCase()) ||
                    item.ADMIN_DESCRIPTION.toString().toLowerCase().includes(value.toLowerCase())

                if (startsWithCondition) {
                    return startsWithCondition
                } else if (!startsWithCondition && includesCondition) {
                    return includesCondition
                } else return null
            })
            this.setState({ filteredData })
        }
    }
    handleSearchFilter = (e) => {
        let value = e.target.value
        let data = this.state.searchdata
        let filteredData = this.state.searchFilteredData
        this.setState({ searchvalue: value });

        if (value.length) {
            filteredData = data.filter(item => {
                let startsWithCondition =
                    item.SYS_STATUS_NAME.toString().toLowerCase().startsWith(value.toLowerCase())

                let includesCondition =
                    item.SYS_STATUS_NAME.toString().toLowerCase().includes(value.toLowerCase())

                if (startsWithCondition) {
                    return startsWithCondition
                } else if (!startsWithCondition && includesCondition) {
                    return includesCondition
                } else return null
            })
            this.setState({ filteredData })
        }
    }

    render() {
        const { data, columns, value, filteredData, searchdata, searchvalue } = this.state
        return (
            <>
                <Helmet><title>Administrative Types</title></Helmet>

                <React.Fragment>
                    <Breadcrumbs
                        breadCrumbTitle="Admin"
                        breadCrumbParent="Setup"
                        breadCrumbActive="Administrative Types"
                    />
                    <Row>
                        <Col sm="12">
                            <Card>
                                <CardHeader>
                                    <CardTitle>Administrative Types</CardTitle>
                                </CardHeader>
                                <CardBody className="rdt_Wrapper">
                                    <SystemStatusTypeSearch
                                        handleSearchSystemStatusType={this.handleCloseSearch}
                                        opensearch={this.state.opensearch}
                                        handleHideSearch={this.handleHideSearch}
                                        value={searchvalue}
                                        data={searchdata}
                                        handleFilter={this.handleSearchFilter}
                                        filteredData={filteredData}
                                    />
                                    <AdministrativeTypesTable
                                        role={this.constructor.name}
                                        handleEdit={this.handleEditData}
                                        handleDelete={this.handleDeleteData}
                                        value={value}
                                        data={data}
                                        columns={columns}
                                        filteredData={filteredData}
                                        handleShow={this.handleShow}
                                        handleFilter={this.handleFilter}
                                    />

                                    <AdministrativeTypesModal
                                        validated={this.state.validated}
                                        show={this.state.show}
                                        handleHide={this.handleHide}
                                        InsertUpdate={this.state.InsertUpdate}
                                        handleSubmit={this.handleSubmit}
                                        handleShowSearch={this.handleShowSearch}

                                        SystemStatusTypeID={this.state.SYS_STATUS_ID}


                                        SystemStatusTypeDescription={this.state.SYSTEMSTATUSTYPEDESCRIPTION}
                                        changeSystemStatusTypeDescription={e => this.setState({ SYSTEMSTATUSTYPEDESCRIPTION: e.target.value })}

                                        Description={this.state.ADMIN_DESCRIPTION}
                                        changeDescription={e => this.setState({ ADMIN_DESCRIPTION: e.target.value })}

                                        ErrorDescription={this.state.errors["Description"]}
                                        ErrorStatus={this.state.errors["Status"]}
                                        showdelete={this.state.showdelete}
                                        handleHideDelete={this.handleHideDelete}
                                        handleDelete={this.handleDelete}
                                    />


                                </CardBody>
                            </Card>
                        </Col>
                    </Row>
                </React.Fragment>

            </>

        )
    }
}

const mapStateToProps = ({ auth: { user } }) => ({
    user
});

export default (connect(mapStateToProps)(AdministrativeTypes));