import React from 'react';
import { Button, Modal, Form, InputGroup } from 'react-bootstrap';
import "../../../../../assets/scss/plugins/forms/react-select/_react-select.scss";
import { Search } from 'react-feather';

class AdministrativeTypesModal extends React.Component {
    render() {
        return (
            <div>
                <Modal
                    show={this.props.show}
                    onHide={this.props.handleHide}
                >
                    <Modal.Header closeButton>
                        <Modal.Title id="AddUpdate">
                            {this.props.InsertUpdate} Administrative Type
                        </Modal.Title>
                    </Modal.Header>
                    <Form noValidate validated={this.props.validated} onSubmit={this.props.handleSubmit} autoComplete="off">
                        <Modal.Body>

                            <Form.Group controlId="Description">
                                <Form.Label>Description</Form.Label>
                                <Form.Control type="text" placeholder="Enter Description"
                                    value={this.props.Description}
                                    onChange={this.props.changeDescription}
                                    name="Description"
                                    required
                                   
                                />
                                <Form.Control.Feedback type="invalid">
                                    Please provide a valid description.
                                 </Form.Control.Feedback>
                                {/* <div className="invalid-feedback d-block">
                                  {this.props.ErrorDescription}
                            </div> */}
                            </Form.Group>

                            <Form.Group controlId="SystemStatusType">
                                <Form.Label>System Status Type</Form.Label>
                                <InputGroup>
                                    
                                    <Form.Control
                                        type="text"
                                        value={this.props.SystemStatusTypeDescription}
                                        // onChange={this.props.handleChangeSystemStatusTypeDescription}
                                        placeholder="Search System Status Type"
                                        aria-describedby="inputGroupPrepend"
                                        required
                                        onChange={this.props.changeSystemStatusTypeDescription}
                                        readOnly
                                        name="Status"
                                    />
                                    <InputGroup.Prepend>
                                        <Button variant="info" size="sm" onClick={this.props.handleShowSearch}>
                                            <Search />
                                        </Button>
                                    </InputGroup.Prepend>
                                </InputGroup>
                                {/* <Form.Control.Feedback type="invalid">
                                {this.props.ErrorStatus}
                                 </Form.Control.Feedback> */}
                                <div className="invalid-feedback d-block">
                                    {this.props.ErrorStatus}
                                </div>
                            </Form.Group>
                        </Modal.Body>

                        <Modal.Footer>
                            <Button variant="success" onClick={this.props.handleHide}>
                                Cancel
                            </Button>
                            <Button variant="primary" type="submit" disabled={this.props.isEnabled}>
                                {this.props.InsertUpdate}
                            </Button>
                        </Modal.Footer>
                    </Form>
                </Modal>

                <Modal
                    show={this.props.showdelete}
                    onHide={this.props.handleHideDelete}
                >
                    <Modal.Header closeButton>
                        <Modal.Title id="AddUpdate">
                            Delete Administrative Type
                        </Modal.Title>
                    </Modal.Header>
                    <Form onSubmit={this.props.handleDelete} autoComplete="off">
                        <Modal.Body>
                            <Form.Group controlId="description">
                                <h4>Are you sure you want to delete this record?</h4>
                                <Form.Control type="text" placeholder="Click Delete Button" value={this.props.Description} onChange={this.props.changeDescription} readOnly />
                            </Form.Group>
                        </Modal.Body>

                        <Modal.Footer>
                            <Button variant="success" onClick={this.props.handleHideDelete}>
                                Cancel
                            </Button>
                            <Button variant="primary" type="submit">
                                Yes Delete
                            </Button>
                        </Modal.Footer>
                    </Form>
                </Modal>
            </div>
        );
    }
}

export default AdministrativeTypesModal;